<template>
	<div class="page-detail">
		<div class="d-inline-block">
			<div v-for="(arr, idx) of desc" :key="idx" class="d-inline-block sec">
				<template v-if="arr.type == 'image'">
					<img :src="arr.url" class="obj w-100" />
				</template>
				<template v-if="arr.type == 'video'">
					<video :src="arr.url" class="w-100" controls autoplay muted loop></video>
				</template>

				<template v-if="desc.length-1 == idx">
					<div class="page-navi">
						<router-link tag="div" :to="'/project/' + prev.path" class="bt-prev">
							Prev
							<div class="title">
								{{ prev.title }}
							</div>
						</router-link>
						<router-link tag="div" :to="'/project/' + next.path" class="bt-next">
							Next
							<div class="title">
								{{ next.title }}
							</div>
						</router-link>
					</div>
				</template>
			</div>
			
		</div>
	</div>
</template>

<script>

export default {
	name: 'Page.Project.Detail',
	data() {
		return {
			works 		: [],
			desc		: [],
			next 		: {
				title	: '',
				path	: '',
			},
			prev 		: {
				title	: '',
				path	: '',
			},
		}
	},
	mounted() {
		this.getData()
		this.setScroll()

		setTimeout(() => {
			var fElem = document.querySelector('.sec:first-child')
			fElem.style.opacity = 1
			fElem.style.transform = 'translateY(0)'
		}, 500)
	},
	methods: {
		getData() {
			this.works = this.$root.works

			for(var i=0; i<this.works.length; i++) {
				if(this.$route.params.title == this.works[i].title.toLowerCase().replace(/\s/g,'-')) {
					this.desc = this.works[i].description
					this.$root.theme = this.works[i].theme

					var startNum = 0
					var endNum = 0

					
					if (i > 0) {
						startNum = i - 1
					}else{
						startNum = this.works.length - 1
					}
					this.prev = {
						title	: this.works[startNum].title,
						path 	: this.works[startNum].title.toLowerCase().replace(/\s/g,'-')
					}

					if(i == this.works.length - 1) {
						endNum = 0
					}else{
						endNum = i + 1
					}

					this.next = {
						title 	: this.works[endNum].title,
						path	: this.works[endNum].title.toLowerCase().replace(/\s/g,'-')
					}
				}
			}
		},
		setScroll() {
			document.addEventListener('scroll',() => {
				const scrTop = document.documentElement.scrollTop
				const secAll = document.querySelectorAll('.sec')
				const scrHeight = screen.height

				for(const sec of secAll) {
					if(!sec.style.opacity) {

						if(scrTop > sec.offsetTop - (scrHeight-200) ) {
							sec.style.opacity = 1
							sec.style.transform = 'translateY(0)'
						}
					}
				}
			})
		},
	},
	watch: {
		$route() {
			this.getData()
		}
	}
}
</script>

<style scoped lang="scss">
    @import '@/assets/scss/project.detail.scss';
</style>